// **************************
// *** Roboto ***
// **************************
$robotoPath: "./fonts/Roboto/";

@font-face {
	font-family: "Roboto";
	src: local("Roboto"),
		url("#{$robotoPath}Roboto-Regular.woff2") format("woff2"),
		url("#{$robotoPath}Roboto-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "Roboto";
	src: local("Roboto"),
		url("#{$robotoPath}Roboto-Medium.woff2") format("woff2"),
		url("#{$robotoPath}Roboto-Medium.woff") format("woff");
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: "Roboto";
	src: local("Roboto"),
		url("#{$robotoPath}Roboto-Bold.woff2") format("woff2"),
		url("#{$robotoPath}Roboto-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
	font-display: swap;
}