.header {
	position: sticky;
	top: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	height: 90px;
	display: flex;
	align-items: center;
	background: #fff;
	box-shadow: 0 0 5px rgba($color: #000000, $alpha: 0.4);

	&:not(&_mb-none):not(&_detail) {
		margin-bottom: 74px;
	}

	&_detail {
		margin-bottom: 30px;
	}

	> .container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__left,
	&__right {
		display: flex;
		align-items: center;
	}

	&__logo {
		margin-left: 70px;
	}

	&__text {
		font-size: 14px;
		line-height: 14px;
		text-transform: uppercase;
		margin: 10px 0 0 10px;

		br {
			display: none;
		}
	}

	&__callback {
		margin-left: auto;
	}

	&__phone {
		margin-left: 48px;
	}

	&__basket {
		margin-left: 22px;
	}

	&__search {
		margin-left: 20px;
	}

	&__langs {
		margin-left: 30px;
	}

	@media (--phone) {
		//   fixed        
		// -           
		//         - position: sticky;
		position: fixed;
		padding: 0;
		height: auto;

		&:not(&_mb-none) {
			margin-bottom: 0;
		}

		&:not(&_mb-none) + &-fixed-height-mob {
			margin-bottom: 20px;
		}
		
		&-fixed-height-mob {
			height: 132px;
			transition: height 0.3s linear;
		}

		&.fixed-mob + &-fixed-height-mob {
			height: 72px;
		}

		&.fixed-mob &__right {
			margin-top: -72px;
		}

		> .container {
			flex-wrap: wrap;
		}

		&__left,
		&__right {
			width: 100%;
		}

		&__left {
			position: relative;
			z-index: 1;
			padding: 20px 0;
			background: #fff;
		}

		&__right {
			padding: 0 0 20px;
			transition: margin-top 0.3s ease-in-out;
		}

		&__menu {
			order: 1;
			margin-left: 25px;
		}
	
		&__logo {
			margin: 0;
		}
	
		&__text {
			position: relative;
			top: -2px;
			line-height: 16px;
			margin: 0 0 0 10px;

			br {
				display: block;
			}
		}

		&__callback {
			order: 1;
			margin-left: 24px;
		}

		&__phone {
			margin-left: auto;
		}

		&__basket {
			margin-left: 25px;
			margin-right: auto;
		}
	
		&__search {
			order: -1;
			margin-left: 0;
		}
	
		&__langs {
			margin-left: 0;
		}
	}

	@media (max-width: 400px) {
		&-fixed-height-mob {
			height: 120px;
		}

		&.fixed-mob + &-fixed-height-mob {
			height: 60px;
		}

		&.fixed-mob &__right {
			margin-top: -60px;
		}

		&__text {
			display: none;
		}

		&__basket {
			margin-left: 20px;
		}

		&__callback {
			margin-left: 18px;
		}
	}
}