.popup-block {
	width: 454px;
	padding: 36px 60px 46px;
	border-radius: 15px;
	background: #fff;

	&__title {
		font-size: 26px;
		font-weight: 700;
		text-align: center;
		margin-bottom: 20px;
	}

	&__text {
		text-align: center;
		margin-bottom: 24px;
	}

	&__fields {
		margin-bottom: 17px;
	}

	&__input {
		margin-bottom: 12px;
	}

	&__textarea {
		height: 111px;
	}

	&__agree {
		margin-bottom: 30px;
	}

	&__btn {
		display: flex;
		justify-content: center;
	}

	@media (--phone) {
		width: 100%;
		max-width: 390px;
		padding: 36px 28px 46px;
	}
}