.langs {
	display: flex;
	align-items: center;

	&_footer & {
		font-size: 14px;
		
		&__lang {
			color: #d6d4d1;

			&.active {
				color: #fff;
			}
		}

		&__separate {
			color: #d6d4d1;
		}
	}
	
	&__lang {
		color: #9a9793;
		font-size: 14px;
		font-weight: 500;
		text-transform: uppercase;

		&.active {
			color: #383838;
		}
	}

	&__separate {
		color: #9a9793;
		font-size: 15px;
		font-weight: 500;
		margin: 0 5px;
	}
}