.subscribe-form {
	width: 100%;

	&_black & {
		&__input {
			height: 48px;
			border: 1px solid #676767;
		}

		&__btn {
			top: 1px;
			right: 1px;
			background: #484541;
		}
	}

	&__title {
		color: #fff;
		font-size: 14px;
		margin-bottom: 6px;
	}

	&__form {
		position: relative;
	}

	&__input {
		border: none;
		width: 100%;
		height: 46px;
		padding: 0 190px 0 24px;
		border-radius: 100px;
		background: #fff;

		&::placeholder {
			color: #d6d4d1;
		}
	}

	&__btn {
		appearance: none;
		cursor: pointer;
		position: absolute;
		top: 0;
		right: 0;
		display: flex;
		align-items: center;
		color: #fff;
		height: 46px;
		padding: 12px 30px;
		border: 1px solid #fff;
		border-radius: 100px;
		background: #9a9793;

		&::after {
			content: '';
			width: 21px;
			height: 16px;
			margin-left: 8px;
			background: url("data:image/svg+xml,%3Csvg viewBox='0 0 21 16' width='21' height='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.928 6.884h15.26l-4.977-4.978A1.116 1.116 0 1 1 13.79.327l6.883 6.884a1.116 1.116 0 0 1 0 1.578l-6.883 6.884a1.112 1.112 0 0 1-1.579 0 1.116 1.116 0 0 1 0-1.579l4.978-4.978H1.928a1.116 1.116 0 0 1 0-2.232z' fill='%23fff'/%3E%3C/svg%3E%0A") no-repeat;
			background-size: contain;
			transition: transform 0.2s ease-in-out;
		}

		&:hover::after {
			transform: translateX(5px);
		}

		&:focus {
			outline: none;
		}
	}

	@media (--desk) {
		&_black & {
			&__input {
				padding-right: 290px;
			}
		}
	}

	@media (--phone) {
		&__btn {
			font-size: 14px;
		}
	}

	@media (max-width: 400px) {
		&__input {
			padding: 0 95px 0 20px;
		}
		
		&__btn {
			font-size: 0;
			line-height: 0;

			&::after {
				margin-left: 0;
			}
		}
	}
}