.arrow-btn {
	position: relative;
	width: 20px;
	height: 20px;
	display: inline-block;

	i {
		position: absolute;
		height: 10%;
		font-style: normal;
		border-radius: 10%;
		background: $color;
		transition: transform  0.2s ease, background-color 0.2s ease;

		&:nth-child(1),
		&:nth-child(2),
		&:nth-child(4),
		&:nth-child(5) {
			width: 50%;
			top: 50%;
		}

		&:nth-child(1) {
			right: 0;
			transform: rotate(-45deg);
			transform-origin: bottom right;
		}
		
		&:nth-child(2) {
			right: 0;
			transform: rotate(45deg);
			transform-origin: top right;
		}
		
		&:nth-child(3) {
			width: 100%;
			top: 50%;
		}

		&:nth-child(4) {
			left: 0;
			transform-origin: bottom left;
		}
		
		&:nth-child(5) {
			left: 0;
			transform-origin: top left;
		}
	}

	.active > & {
		i {
			&:nth-child(1) {
				transform: rotate(0);
			}

			&:nth-child(2) {
				transform: rotate(0);
			}

			&:nth-child(4) {
				transform: rotate(-45deg);
			}
			
			&:nth-child(5) {
				transform: rotate(45deg);
			}
		}
	}
}
