.hamburger {
	cursor: pointer;
	position: relative;
    display: block;
    width: 35px;
    height: 20px;

	&__inner {
		top: 50%;
		margin-top: -1.5px;
		transition: transform 75ms cubic-bezier(.55,.055,.675,.19);

		&,
		&:before,
		&:after {
			position: absolute;
			width: 100%;
			height: 3px;
			display: block;
			border-radius: 3px;
			background: #4b4b4b;
		}

		&:before,
		&::after {
			content: '';
		}

		&:before {
			top: -9px;
			transition: top 75ms ease 0.12s, opacity 75ms ease;
		}

		&:after {
			bottom: -9px;
			transition: bottom 75ms ease 0.12s, transform 75ms cubic-bezier(.55,.055,.675,.19);
		}
	}

	&.active & {
		&__inner {
			transform: rotate(45deg);
			transition: transform 75ms cubic-bezier(.215,.61,.355,1) 0.12s;

			&::before {
				top: 0;
				opacity: 0;
				transition: top 75ms ease, opacity 75ms ease 0.12s;
			}

			&::after {
				bottom: 0;
				transform: rotate(-90deg);
				transition: bottom 75ms ease, transform 75ms cubic-bezier(.215,.61,.355,1) 0.12s;
			}
		}
	}

	@media (--phone) {
		width: 30px;
		height: 18px;

		&__inner {
			&:before {
				top: -8px;
			}
	
			&:after {
				bottom: -8px;
			}
		}
	}
}