.footer {
	color: #fff;
	overflow-x: hidden;
	padding: 30px 0 20px;
	background: #9a9793;

	&_vawe {
		padding-top: 80px;
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 12px;
	}

	&__middle {
		margin: 0 83px 56px 240px;
	}

	&__subscribe-form {
		width: 450px;
		margin: 0 auto 58px;
	}

	&__bottom {
		margin: 0 83px 20px 267px;
	}

	&__copyright {
		color: #fff;
		font-size: 12px;
	}

	@media (--phone) {
		&__top {
			margin-bottom: 45px;
		}

		&__middle {
			margin: 0 0 50px;
		}

		&__subscribe-form {
			width: 100%;
			margin: 0 0 50px;
		}
	
		&__bottom {
			margin: 0 0 40px;
		}
	}
}