.alert {
	position: relative;
	z-index: 2;
	padding: 97px 0;
	min-height: 100vh;
	margin-bottom: -58px;
	background: url('../images/alert/pic.png') no-repeat;
	background-size: cover;
	background-position: center bottom;

	&__title {
		font-size: 56px;
		font-weight: 700;
		line-height: 66px;
		margin-bottom: 25px;
	}

	&__text {
		max-width: 390px;
		line-height: 22px;
		margin-bottom: 60px;
	}

	@media (--phone) {
		min-height: auto;
		padding: 50px 0 233px;
		background-image: url('../images/alert/pic-mob.png');

		&__title {
			font-size: 46px;
			line-height: normal;
			margin-bottom: 20px;
		}

		&__text {
			margin-bottom: 40px;

			br {
				display: none;
			}
		}
	}
}