.popup {
	&_video & {
		&__close {
			background: #fff;
		}

		&__block {
			position: static;
			width: 100%;
			max-width: 950px;
		}

		&__change {
			position: relative;
			width: 100%;
			height: 0;
			background: #000;
			padding-bottom: 56.25%;

			iframe {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border: 0;
			}
		}
	}

	&:not(&_video) & {
		&__close {
			top: 8px;
			right: 10px;

			&::before,
			&::after {
				width: 20px;
				margin: -1px 0 0 -10px;
				background: #d6d4d1;
			}
		}
	}

	&_mob-full & {
		&__table {
			padding: 0;
		}

		&__block {
			display: block;
			width: 100%;
			height: 100%;
		}

		&__change {
			height: 100%;
		}
	}
}